@import "https://fonts.googleapis.com/css2?family=Manrope:wght@200..800&family=Plus+Jakarta+Sans:ital,wght@0,200..800;1,200..800&display=swap";
:root {
  --content-width: 1330px;
  --content-width--small: 1200px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width)  + (var(--container-offset) * 2));
  --container-width--small: calc(var(--content-width--small)  + (var(--container-offset) * 2));
  --font-family-primary: "Manrope", sans-serif;
  --font-family-secondary: "Plus Jakarta Sans", sans-serif;
  --fw-400: 400;
  --fw-500: 500;
  --fw-600: 600;
  --fw-700: 700;
  --fw-800: 800;
  --color-white: #fff;
  --color-white-50: #ebeaed;
  --color-white-100: #ffffff1a;
  --color-white-200: #fff3;
  --color-white-500: #ffffff0d;
  --color-gray-300: #bdbdbd;
  --color-gray-400: #828282;
  --color-black: #000;
  --color-black-700: #2d2d2d;
  --color-black-750: #111827;
  --color-black-800: #161616;
  --color-black-850: #161616cc;
  --color-blue-50: #e8eeff;
  --color-blue-200: #87a9fa;
  --color-blue-300: #5488ff;
  --color-blue-400: #235ee7;
  --color-blue-800: #010e35;
  --radius-main: 4px;
}

.checkbox__field:checked + .checkbox__content:after {
  opacity: 1;
}

.checkbox__field:focus + .checkbox__content:before {
  outline: 1px solid var(--color-gray-100);
  outline-offset: 1px;
}

.checkbox__field:disabled + .checkbox__content {
  opacity: .4;
  pointer-events: none;
}

html, body {
  scroll-behavior: smooth;
  overscroll-behavior-y: none;
}

.page {
  font-family: var(--font-family-primary);
  font-weight: var(--fw-700);
  letter-spacing: 0;
  background-color: var(--color-white);
  font-optical-sizing: auto;
  font-variation-settings: "wdth" 100;
  font-size: clamp(16px, 1vw, 20px);
  font-style: normal;
  line-height: 1;
}

.page__body {
  min-height: var(--vh);
  min-width: 320px;
}

.page.open {
  overflow: hidden;
}

.container {
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
  width: 100%;
  margin: 0 auto;
}

.container--small {
  max-width: var(--container-width--small);
}

.site-container {
  grid-template-rows: 1fr auto;
  grid-template-columns: 100%;
  display: grid;
}

.background-light {
  background-color: var(--color-white-50);
}

.logo {
  width: 100%;
  max-width: 320px;
}

.radial-gradient:after {
  z-index: -2;
  content: "";
  background: radial-gradient(circle at 0 100%, #235ee71a 0%, #235ee700 35%);
  width: 100%;
  height: 100%;
  position: absolute;
}

.radial-gradient--bottom:after {
  bottom: 0;
  left: 0;
}

.radial-gradient--top:after {
  background: radial-gradient(circle at 0 0, #235ee71a 0%, #235ee700 35%);
  top: 0;
  left: 0;
}

.radial-gradient--right:after {
  background: radial-gradient(circle at 100%, #235ee71a 0%, #235ee700 35%);
  top: 0;
  right: 0;
}

.radial-gradient--white:after {
  background: radial-gradient(circle at 100% 0, #ffffff1a 0%, #fff0 35%);
  top: 0;
  right: 0;
}

.description {
  color: var(--color-blue-400);
  text-transform: uppercase;
  letter-spacing: .6px;
  margin-bottom: 20px;
  padding-left: 68px;
  font-size: 24px;
  line-height: 1.4;
  position: relative;
}

.description:before {
  content: "";
  background-color: var(--color-blue-400);
  border: none;
  width: 48px;
  height: 1px;
  position: absolute;
  top: 50%;
  left: 4px;
  transform: translateY(-50%);
}

.item {
  border-radius: var(--radius-main);
  background-color: var(--color-black-800);
  color: var(--color-white);
  width: 100%;
  padding: 20px 20px 20px 89px;
  position: relative;
}

.item:before {
  content: "";
  background-image: url("../images/icons/check.svg");
  background-repeat: no-repeat;
  background-size: contain;
  width: 25px;
  height: 25px;
  position: absolute;
  top: 50%;
  left: 32px;
  transform: translateY(-50%);
}

.hidden {
  display: none !important;
}

.sr-only {
  -webkit-clip-path: inset(50%) !important;
  clip-path: inset(50%) !important;
  white-space: nowrap !important;
  clip: rect(1px, 1px, 1px, 1px) !important;
  border: 0 !important;
  width: 1px !important;
  height: 1px !important;
  margin: -1px !important;
  padding: 0 !important;
  position: absolute !important;
  overflow: hidden !important;
}

.dis-scroll {
  height: calc(var(--vh, 1vh) * 100);
  overscroll-behavior: none;
  width: 100%;
  height: 100dvh;
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

h1, h2, h3 {
  font-weight: var(--fw-700);
  color: var(--color-white);
  text-transform: capitalize;
  letter-spacing: 1px;
  width: fit-content;
  line-height: 1.3;
  overflow: visible;
}

h1 span, h2 span, h3 span {
